footer .footer-body{
    background-color: #2a3043;
    padding: 56px 74px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
footer .footer-body .left-side img{
    width: 217.9px;
    height: 38.42px;
}
footer .footer-body .left-side{
    display: flex;
    flex-direction: column;
    gap: 32px;
}
footer .footer-body .left-side ul{
    display: flex;
    flex-direction: column;
    gap: 12px;
}
footer .footer-body .left-side ul li{
    font-size: 14px;
    font-weight: 600;
    line-height: 14px;
    color: #BBBBBB;
}
footer .footer-body .left-side ul li.gray{
    color: #98A0A8;
}
footer .footer-body .right-side button{
    display: flex;
    align-items: center;
    border-radius: 64px;
    border: 1px solid #1D9BF0;
    background-color: #292C3D;
    padding: 16px 32px;
    gap: 12px;
    color: #98A0A8;
    font-size: 18px;
    line-height: 18px;
    font-weight: 500;
    cursor: pointer;
}
footer .privacy{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px;
    background-color: #232637;
}
footer .privacy p{
    font-size: 12px;
    line-height: 12px;
    font-weight: 500;
    color: #98A0A8;
}
@media only screen and (max-width: 360px) {
    footer .footer-body{
        flex-direction: column;
        justify-content: center;
        padding: 30px;
        gap: 30px;
    }
    footer.isClose{
        display: none;
    }
    footer .footer-body .container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 30px;
    }
    footer .footer-body .right-side button{
        border-radius: 48px;
        background-color: #141526;
        gap: 9px;
        padding: 18px 30px;
        border: none;
    }
    footer .footer-body .left-side ul{
        justify-content: center;
        align-items: center;
        gap: 16pxpx;
    }
    footer .footer-body .left-side ul li{
        font-weight: 500;
        color: #eeeeee;
    }
    footer .footer-body .left-side ul li.gray{
        color: #eeeeee;
    }
}