#middle-body {
    margin-left: 317.8px;
    margin-right: 317.8px;
    overflow-y: auto;
    width: calc(100% - 635.6px);
}
#middle-body .title:nth-child(1) h3{
    font-weight: 600;
    font-size: 36px;
    line-height: 36px;
}
#middle-body .title{
    padding: 12px 16px;
}
#middle-body .content p{
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    letter-spacing: 2%;
    color: #FFFFFF;
}
#middle-body .content{
    padding: 12px 16px;
}
#middle-body .empty-container{
    width: 100%;
    height: 80px;
}
#middle-body .title h5{
    font-weight: 600;
    font-size: 24px;
    line-height: 24px;
    color: #FFFFFF;
}
#middle-body .section-content{
    gap: 8px;
    padding: 12px 16px;
}
#middle-body .section-content p{
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 2%;
    color: #EEEEEE;
}
#middle-body .section-content p span{
    color: #1D9BF0;
}
#join-community .items .item{
    display: flex;
    flex-direction: column;
    gap: 16px;
    border-radius: 24px;
    background-color: #2a3043;
    padding: 32px;
}
#join-community .items .item h6{
    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    color: #EEEEEE;
}
#join-community .items .item p{
    font-weight: 500;
    font-size: 12px;
    line-height: 15.6px;
    color: #98A0A8;
    width: 200.33px;
}
#join-community .items .item .white{
    color: #FFFFFF !important;
}
#join-community .items{
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    padding: 16px;
}
@media only screen and (max-width: 360px) {
    #middle-body {
        overflow-y: auto;
        width: 100%;
        margin: 0;
    }
    #middle-body.isClose{
        display: none;
    }
    #join-community .items .item{
        width: 100%;
    }
    #join-community .items .item p{
        width: 100%;
    }
}