#right-bar{
    padding: 16px;
    position: fixed;
    right: 0;
}
#right-bar .search{
    background-color: #3d4563;
    display: flex;
    gap: 8px;
    border-radius: 40px;
    padding: 8px 10px;
    align-items: center;
}
#right-bar .search button svg{
    width: 16px;
    height: 16px;
    color: #E8F1F2;
}
#right-bar .search button{
    border: none;
    outline: none;
    background-color: transparent;
}
#right-bar .search input{
    background-color: transparent;
    outline: none;
    border: none;
    color: #E8F1F2;
}
#right-bar .search input::placeholder{
    color: #E8F1F2;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    letter-spacing: 0.2px;
}
#right-bar .divider{
    height: 1px;
    width: 238px;
    opacity: 50%;
    background-color: #1D9BF0;
}
.secondMenuItems nav ul {
    display: flex;
    flex-direction: column;
    gap: 4px;
    padding: 12px 0;
}
.secondMenuItems nav ul li{
    padding: 8px 12px;
}
.secondMenuItems nav ul li a{
    color: #BBBBBB;
    font-weight: 600;
    font-size: 14px;
    line-height: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media only screen and (max-width: 360px) {
    #right-bar {
        height: 0px;
        position: static;
        transform: translateX(200%);
        transition: all ease-in 200ms;
    }
    #right-bar.isOpen{
        transform: translateY(0);
        height: 100vh;
    }
}

