#left-bar{
    position: fixed;
    left: 0;
}
#left-bar .leftBar-container{
    padding: 16px;
    display: flex;
    flex-direction: column;
    gap: 24px;
}
#left-bar .change .chapleanApp{
    display: flex;
    gap: 8px;
}
#left-bar .change .chapleanApp .chaplean-platform h2,h3{
    color: #ffffff;
}
#left-bar .change .chapleanApp .chaplean-platform h2{
    line-height: 16px;
    font-weight: 500;
    font-size: 16px;
}
#left-bar .change .chapleanApp .chaplean-platform h3{
    line-height: 12px;
    font-weight: 400;
    font-size: 12px;
}
#left-bar .change .chapleanApp .chaplean-platform{
    width: 135px;
    display: flex;
    flex-direction: column;
    gap: 6px;
}
#left-bar .change{
    display: flex;
    justify-content: space-between;
    border: 1px solid #1D9BF0B2;
    border-radius: 12px;
    padding: 16px;
    height: 66px;
    background-color: #3d4563;
    width: 269px;
}
#left-bar .change .arrow-down{
    cursor: pointer;
}
@media only screen and (max-width: 360px) {
    #left-bar {
        position: static;
        height: 0px;
        transform: translateX(-200%);
        transition: all ease-in 200ms;
    }
    #left-bar .change{
        width: 100%;
    }
    #left-bar.isOpen{
        transform: translateY(0);
        height: 100vh;
    }
}