#sub-header{
    width: 100%;
    background-color: #3d4563;
    display: none;
}
#sub-header .container{
    padding: 16px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
}
#sub-header .container .menu-click,.search-click{
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: #FFFFFF;
    cursor: pointer;
    background-color: transparent;
    cursor: pointer;
}
#sub-header .back-container{
    padding: 8px;
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 14px;
    line-height: 14px;
    font-weight: 400;
    color: #FFFFFF;
    cursor: pointer;
}
@media only screen and (max-width: 360px) {
    #sub-header{
        display: block;
    }
    #sub-header .container.show{
        display: flex;
    }
    #sub-header .container.hidden{
        display: none;
    }
    #sub-header .back.show{
        display: block;
    }
    #sub-header .back.hidden{
        display: none;
    }

}