.sidebar-menu nav ul {
    display: flex;
    flex-direction: column;
    gap: 4px;
    padding: 12px 0;
}
.sidebar-menu nav ul li{
    padding: 8px 12px;
}
.sidebar-menu nav ul li a{
    color: #BBBBBB;
    font-weight: 600;
    font-size: 14px;
    line-height: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.sidebar-menu nav ul li a svg{
    width: 20px;
    height: 20px;
}
.sidebar-menu nav ul li:nth-child(1) a{
    color: #E8F1F2;
}
.sidebar-menu{
    width: 269px;
}
