header .header-container{
    padding: 10px 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2e344b;
}
header .logo img{
    width: 174px;
    height: 30.74px;
}
header .header-menu nav ul
,.header-menu-mobile nav ul{
    display: flex;
    gap: 32px;
}
header .header-menu nav ul li a,
.header-menu-mobile nav ul li a{
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    letter-spacing: 0.2px;
    color: #EEEEEE;
    cursor: pointer;
}
header .header-menu nav ul li:nth-child(4) a,
.header-menu-mobile nav ul li:nth-child(3) a{
    color: #1D9BF0;
    text-decoration: underline;
    font-weight: 500;
}
header .header-menu nav ul li:nth-child(5) a
,.header-menu-mobile nav ul li:nth-child(5) a{
    color: #141526;
    background-color: #1D9BF0E5;
    padding: 8px 16px;
    border-radius: 40px;
}
.header-menu-mobile nav ul li a{
    font-weight: 500;
}
.header-menu-mobile nav ul li:nth-child(5) a{
    font-weight: 600;
    border-radius: 52.5px;
}

header .burger-menu{
    display: none;
}
header .burger-menu img{
    cursor: pointer;
}
header .header-menu-mobile{
    transform: translateY(-200%);
    position: absolute;
    width: 100%;
}
header .header-menu-mobile nav ul{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #272C46;
    gap: 24px;
    padding: 16px 0;
}
header .header-menu-mobile.show{
    display: block;
    transition: transform ease-in 200ms;
    transform: translateY(0%);
}
@media only screen and (max-width: 360px) {
    header  .header-menu {
        display: none;
    }
    header .burger-menu{
        display: block;
    }
}
@media only screen and (min-width: 361px) {
    header .header-menu-mobile{
        display: none;
    }
}
